import React from 'react'
import { Header } from '../components'
import  Tasks  from './tasks'
import Tags from './tags'
import { NewTask } from './tasks'


class App extends React.Component {

	constructor() {
		super()
	}

	render() {
		return (
      <div>
        <Header />
				<Tags addTag={this.addTag}/>
        <Tasks />
				<NewTask />
      </div>
		)
	}

}

export default App



